<template>
    <div>
        <div class="description-style">
            欢迎来到ashine，这是我的首页，下面都是demo而已
        </div>
        <div id="chart1"></div>
    </div>
</template>
<script>
// import { Chart } from 'viser-vue'
// import {  } from "@antv";
export default {
    name: "MySecView",
    data(){
        return {
            chartData: [
                { genre: 'Sports', sold: 275 },
                { genre: 'Strategy', sold: 115 },
                { genre: 'Action', sold: 120 },
                { genre: 'Shooter', sold: 350 },
                { genre: 'Other', sold: 150 }
            ]
        }
    },
    methods: {
        updateDivChart(chart, chartData){
            // chart.clear()
            let newData = []
            for (let index = 0; index < chartData.length; index++) {
                const element = chartData[index];
                let create_date = element.create_date
                let day = new Date(create_date).getDate()
                newData.push({
                    create_date: `${day}`,
                    catt: '订单数',
                    value: element.num
                })
                newData.push({
                    create_date: `${day}`,
                    catt: '分销金额',
                    value: element.money
                })

            }
        
            chart.source(newData, {});
            chart.axis('create_date', {
                label: {
                textStyle: {
                    fill: '#aaaaaa'
                    }
                },
                 tickLine: {
                    alignWithLabel: false,
                    length: 0
                }
            })
             chart.axis('value', {
                label: {
                textStyle: {
                    fill: '#aaaaaa'
                }
                },
                title: {
                offset: 50
                }
            })
            chart
            .interval()
            .position('create_date*value')
            .color('catt')
            .opacity(1)
            .adjust([{
                type: 'dodge',
                marginRatio: 1 / 32
            }]);
            chart.render();
        },
        init1() {
             let data = [
                 {
                    create_date: "2019-01-01", //日期
                    num: 6,    //订单数
                    money: 14.43  //分销金额
                    },
                    {
                    create_date: "2019-01-02",
                    num: 3,
                    money: 34.43
                    }
             ]
        let chart = new G2.Chart({
                container: 'chart1',
                // width: 600,
                // height: 300,
                forceFit: true,
                height: 350,
                padding: [ 40, 120, 80, 120],
                // theme: "dark"
        })
        this.updateDivChart(chart, data)
        },
    },
    mounted(){
        this.init1()
        // new G2.Chart({

        // })
        // console.log("G2.version = ", G2.version);
        
        // const chart = new G2.Chart({
        //     container: 'chart1',
        //     width: 600,
        //     height: 300
        // })
        // // Step 2: 载入数据源
        // chart.source(this.chartData);
        // // Step 3：创建图形语法，绘制柱状图，由 genre 和 sold 两个属性决定图形位置，genre 映射至 x 轴，sold 映射至 y 轴
        // chart.interval().position('genre*sold').color('red')
        // // Step 4: 渲染图表
        // chart.render();

    }
}
</script>
<style scoped>
.description-style {
    text-align: center;
    color: whitesmoke;
    background-color:black;
    padding-top: 20px;
    padding-bottom: 30px;
    margin-bottom: 20px;
    margin-top: 30px;
}
</style>



